import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { withRouter } from "react-router";
import { getApartment } from "api/apartments";
import PhotoBar from "components/ApartmentView/PhotoBar";
import ApartmentInfo from "components/ApartmentView/ApartmentInfo";
import { inject, observer } from "mobx-react";

const ApartmentView = ({ match, userStore }) => {

    const [ apartment, setApartment ] = useState(null);
    useEffect(() => {
        getApartment(match.params.id).then(data => {
            setApartment(data);
            if (userStore.isLogged) userStore.user.addVisitedApartment(data._id);
        })
    }, [ match.params.id, userStore.isLogged, userStore.user ])

    if (apartment) return (
        <Container>
            {apartment.photos.length ? <PhotoBar photos={apartment.photos}/> : <h2>Фотографії відсутні</h2>}
            <ApartmentInfo apartment={apartment} isLogged={userStore.isLogged} hasSubscription={userStore.user.hasSubscription} />
        </Container>
    )
    return (
        <div></div>
    )
}

export default inject('userStore' )(withRouter(observer(ApartmentView)));

const Container = styled.div`
  max-width: ${props => props.theme.consts.containerWidth};
  margin: auto;
`;
