import React, {useRef, useState} from 'react';
import styled from "styled-components";
import { Galleria } from 'primereact/galleria';

const thumbnailTemplate = item => {
    const url = item.formats ? item.formats.small ? item.formats.small.url : item.url : item.url;
    return <img src={process.env.REACT_APP_DOMAIN_URL + url} alt={item.alt} style={{ display: 'block' }} />;
}

const itemTemplate = item => {
    return <img src={process.env.REACT_APP_DOMAIN_URL + item.url} alt={item.alt} style={{ width: '100%', display: 'block' }} />
}

const PhotoBar = ({photos}) => {

    const galleria = useRef(null);
    const [ activeIndex, setActiveIndex ] = useState(0);

    const mainPhotos = photos.slice(0,3);
    return (
        <>
            <Galleria ref={galleria}
                      value={photos}
                      activeIndex={activeIndex}
                      onItemChange={(e) => setActiveIndex(e.index)}
                      circular
                      fullScreen
                      showItemNavigators
                      showThumbnails={false}
                      item={itemTemplate}
                      thumbnail={thumbnailTemplate}
            />
            <Container>
                <img
                    src={process.env.REACT_APP_DOMAIN_URL + (mainPhotos[0].formats ? mainPhotos[0].formats.small ? mainPhotos[0].formats.small.url : mainPhotos[0].url : mainPhotos[0].url)}
                    key={0}
                    style={{ cursor: 'pointer' }}
                    onClick={() => {
                        setActiveIndex(0);
                        galleria.current.show();
                    }}
                    alt="фото квартири"
                />
                {mainPhotos[1] && <img
                    src={process.env.REACT_APP_DOMAIN_URL + (mainPhotos[1].formats ? mainPhotos[1].formats.small ? mainPhotos[1].formats.small.url : mainPhotos[1].url : mainPhotos[1].url)}
                    key={1}
                    style={{cursor: 'pointer'}}
                    onClick={() => {
                        setActiveIndex(1);
                        galleria.current.show();
                    }}
                    alt="фото квартири"
                />}
                {mainPhotos[2] && <PhotoWrapper onClick={() => {
                    setActiveIndex(2);
                    galleria.current.show();
                }}>
                    <img
                        src={process.env.REACT_APP_DOMAIN_URL + (mainPhotos[2].formats ? mainPhotos[2].formats.small ? mainPhotos[2].formats.small.url : mainPhotos[2].url : mainPhotos[2].url)}
                        key={2}
                        style={{cursor: 'pointer'}}
                        alt="фото квартири"
                    />
                    <div/>
                    <span> + {photos.length - 2} фото</span>
                </PhotoWrapper>}
            </Container>
        </>

    )
}

export default PhotoBar;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  
  img {
    height: 300px;
    object-fit: cover;
  }
  
  @media(min-width: 968px) {
    display: grid;
    justify-items: stretch;
    overflow: hidden;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 200px 200px;
    height: 400px;

    img {
      display: block;
      object-fit: cover;
    }
    img:nth-child(1) {
      grid-row: 1/3;
      height: 100%;
    }
    img:nth-child(2) {
      grid-column: 2/3;
    }
  }
  
`;

const PhotoWrapper = styled.div`
  grid-column: 2/3;
  grid-row: 2/3;
  position: relative;
  display: grid;
  justify-items: stretch;
  cursor: pointer;
  img {
    display: block;
    object-fit: cover;
  }

  div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
  }

  span {
    position: absolute;
    font-size: 3rem;
    color: white;
    z-index: 2;
    margin: auto;
    width: 100%;
    text-align: center;
    top: calc(50% - 1.5rem);
  }
`;