import React, { useState } from 'react';
import styled from 'styled-components';
import moment from 'moment';
import robot from 'images/robot.png';
import ok from 'images/ok.png';
import { Link } from "react-router-dom";
import { routes } from "consts/routes";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { SNIPPETS } from "consts/styles";
import { withRouter } from "react-router";
import GetPhoneModal from "components/ApartmentView/GetPhoneModal";

const ApartmentInfo = ({ apartment, isLogged, hasSubscription, history }) => {
    const { createdAt, name, room, rooms_amount, square, full_square, moderated, description, price, currency, address, youtube_link, _id, phone_number } = apartment;
    const [ isPhoneModalVisible, setPhoneModalVisibility ] = useState(false);
    const [ isPhoneVisible, setPhoneVisibility ] = useState(false);
    function handlePhoneClick() {
        if (isLogged) {
            if (hasSubscription) {
                setPhoneVisibility(true);
            } else {
                history.push(routes.TARRIFS);
            }
        } else setPhoneModalVisibility(true);
    }

    function getFloor(full_square) {
        const data = full_square.split("/");
        let res = '';
        if (parseInt(data[0])) res += parseInt(data[0]);
        if (parseInt(data[1])) res += `/${parseInt(data[1])}`;
        return res;
    }

    const floor = getFloor(full_square);

    return (
        <>
            <CreatedLabel>{moment(createdAt).locale("uk").fromNow()}, {moment(createdAt).locale("uk").format("d MMM., HH:mm")}</CreatedLabel>
            <Container>
                <div>
                    <h3>{name}</h3>
                    <hr size="1px"/>
                    <ApartmentCharacters>
                        {!room && <div className="character">
                            <h3>{rooms_amount}<span className="small">-к</span></h3>
                            <span>квартира</span>
                        </div>}
                        <div className="character">
                            <h3>{square}<span className="small">м2</span></h3>
                            <span>площа {room ? 'кімнати' : 'квартири'}</span>
                        </div>
                        {floor && <div className="character">
                            <h3>{floor}</h3>
                            <span>поверх</span>
                        </div>}
                        <div className="character">
                            <img src={moderated ? ok : robot} alt=""/>
                        </div>
                        {youtube_link !== 'none' && <div className="character">
                            <a href={youtube_link}><i className='bx bxl-youtube'/></a>
                        </div>}
                    </ApartmentCharacters>
                    {!moderated && <Unmoderated>
                        <p>Оголошення перевірене роботом</p>
                        <p>Отримайте доступ до всіх оголошень або доручіть пошук особистому помічникові.</p>
                        <Link to={routes.TARRIFS}>Розпочати</Link>
                    </Unmoderated>}
                    <h3>Опис</h3>
                    <p className="lead">{description}</p>
                </div>
                <PriceCard>
                    <h3>{price}<span className="small">{currency==="UAH"?"₴":currency==="USD"?"$":"€"}/місяць</span></h3>
                    <p>{address}</p>
                    {!isLogged && <>
                        <h4>Сподобалася квартира?</h4>
                        <h4>Залишай заявку і ми надійшлемо тобі номер власника!</h4>
                        <Button onClick={handlePhoneClick}>Залишити заявку</Button>
                    </>}
                    {isLogged && <>
                        {isPhoneVisible ?
                            <Phone href={`tel:${phone_number}`}>{phone_number}</Phone>
                            : <Button onClick={handlePhoneClick}>
                                <i className="bx bx-phone bx-tada"/>Телефон власника
                            </Button>}
                    </>}
                </PriceCard>
            </Container>
            <GetPhoneModal
                visible={isPhoneModalVisible}
                onHide={() => setPhoneModalVisibility(false)}
                _id={_id}
            />
        </>
    )
}

export default withRouter(ApartmentInfo);

const Container = styled.div`
  display: grid;
  grid-gap: 20px;
  padding: 20px;
  @media(min-width: 1000px) {
    grid-template-columns: 2fr 1fr;  
  }
  h3 {
    font-size: 1.5rem;
  }
  hr {
    color: #ccc;
    opacity: .25;
  }
  .lead {
    font-weight: 300;
  }
  
  span.small {
    font-size: .8rem;
  }
`;

const ApartmentCharacters = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  div.character {
    width: max-content;
    max-width: 100%;
    padding: 20px 25px;
    display: block;
    text-align: center;

    h3 {
      font-size: 2rem;
      margin-top: 0;
      margin-bottom: 16px;
    }

    a {
      color: #e20101;
      font-size: 4rem;

      &:hover {
        color: #ee0202;
      }

      &:active {
        color: #ea0000;
      }
    }
  }
`;

const CreatedLabel = styled.span`
  color: ${props => props.theme.colors.text.disabled};
  font-size: .9rem;
`;

const Unmoderated = styled.div`
  background: ${props => props.theme.colors.primary.hover};
  padding: 16px;
  border-radius: 3px;
  border: 1px solid orange;
  
  p {
    margin: 0;
    text-align: center;
  }
  a {
    width: 100%;
    background: #fff;
    color: ${props => props.theme.colors.text.important};
    display: block;
    text-align: center;
    padding: 6px;
    border-radius: 3px;
    cursor: pointer;
    text-decoration: none;
    margin-top: 16px;
  }
`;

const PriceCard = styled(Card)`
  height: max-content;
  text-align: center;
  color: ${props => props.theme.colors.text.important};
  h3 {
    font-size: 1.75rem;
    line-height: 1.1;
    margin: 0;
    font-weight: 700;
  }
  p {
    font-size: .8rem;
  }
  h4 {
    margin: 0 0 5px 0;
    font-size: 1rem;
  }
  button {
    ${SNIPPETS.noOutline};
    border: none;
    background: ${props => props.theme.colors.warning.main};
    color: ${props => props.theme.colors.warning.text} !important;
    display: block;
    width: 90%;
    margin: auto;
    padding: 8px;
    font-size: 1.2rem;
    font-weight: bold;
    padding: .6rem 1.2rem;
    &:hover {
      background: ${props => props.theme.colors.warning.hover} !important;
    }
    &:active {
      background: ${props => props.theme.colors.warning.active} !important;
    }
  }
`;

const Phone = styled.a`
  ${SNIPPETS.linkColor};
  font-size: 2rem;
  text-decoration: none;
`;
